@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600&display=swap);
.header {
    height: 100px;
    width: 100%;
    padding: 10px 20px;
    display: flex;
    justify-content: space-between;
    background-color: #fff;
}

.header .logo-container { /* div que segura a logo */
    height: 80px;
    width: auto;
}

.header .logo-container .logo { /* Logo do Header */
    height: 80px;
}

.header .options {
    justify-content: flex-end;
}

.background-fixed {
    position: fixed;
    width: 100%;
    height: 430px;
    background-image: url(https://onsurance.me/wp-content/uploads/2019/08/insurance-35.png);
    background-repeat: no-repeat;
    background-position: left top;
    z-index: 0;
}

.header .options .option { /* Opções do header */
    padding: 10px 15px;
    text-decoration: none;
    font-size: 14px;
    color: #2991d6;
    cursor: pointer;
    letter-spacing: 1px;
}

.header .options .option:hover { /* Opções do header */
    color: #464646;
    -webkit-transition: all 300ms;
    transition: all 300ms;
    border-bottom: 1px solid #000;
}

.link-externo {
    text-decoration: none;
    text-align: center;
}
.conteudo {
    width: 100%;
    padding-bottom: 30px;
}

@media screen and (min-width: 1024px) {
    .conteudo {
        width: 100%;
        max-width: 900px;
        padding-bottom: 30px;
    }
}

.centralizado {
    text-align: center;
}

.card-main {
    background-color: #fff;
    box-shadow: 0px 2px 8px -2px rgba(140,140,140,1);
    max-height: 3000px;
    width: 100%;
}

.centralizar {
    display: flex;
    justify-content: center;
}
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  height: 100%;
}

#root{
  height: 100%;
}

.wrapper-app {
  min-height: 100%;
  position: relative;
  background-color: #F7F8FC
}

h2 {
  font-family: 'Montserrat', sans-serif;
  color: #333;
}

.container {
  padding-top: 40px;
  display: flex;
  justify-content: center;
  min-height: 100%;
  height: 100%;
}

.body-content {
  padding-bottom: 50px;
}
